.header {
  background-color: #757575aa;
  border-bottom: thin solid lightgray;
  height: 7vh;
  width: 90vw;
  min-height: 30px;
  position: fixed;
  left: 5vw;
  top: 0;
  z-index: 1;
}

.header-title {
  position: relative;
  font-family: "Times New Roman", Times, serif;
  font-size: 3vw;
  left: 1vw;
}

.nav-button {
  background-color: lightblue;
  position: relative;
  padding: 1vh 1vw;
  margin-right: 1vw;
  float: right;
  right: 3vw;
  top: 1vh;
  border-radius: 10%;
  cursor: pointer;
}

.nav-button:hover {
  background-color: yellow;
}

.profile-pic-container {
  width: 67vw;
  margin: 12vh auto;
}

.profile-pic {
  background-image: url("data:image/jpeg;base64,");
  background-size: cover;
  background-repeat: no-repeat;
  margin: auto;
  height: 30vh;
  width: 30vh;
  border-radius: 50%;
  color: red;
}

.about-container {
  border-top: thin solid lightgray;
  width: 67vw;
  margin: 5vh auto;
}

.cover-paragraph {
  font-size: 12px;
  text-indent: 3vw;
}

.projects-container {
  border-top: thin solid lightgray;
  height: 80vh;
  width: 67vw;
  margin: 5vh auto;
}

.ant-carousel .slick-slide {
  text-align: center;
  height: 60vh;
  line-height: 60vh;
  background: #364d79;
  overflow: hidden;
}

.ant-carousel .slick-slide h3 {
  color: #fff;
}

.contact-container {
  border-top: thin solid lightgray;
  height: 40vh;
  width: 67vw;
  margin: 5vh auto;
}

.email-text-entry {
  height: 60vh;
  width: 67vw;
  overflow: hidden;
}
